export default {
  methods: {
    scrollBack() {
      document
        .getElementById("lookbook-icon")
        .classList.remove("nav-blue-active");
      document
        .querySelector(".nav-lookbook")
        .classList.remove("nav-blue-active");
      document
        .getElementById("manifestoicon")
        .classList.remove("nav-blue-active");
      document
        .querySelector(".nav-manifesto")
        .classList.remove("nav-blue-active");
      let container = document.getElementById("container");

      if (this.$route.path === "/") {
        container.scrollTo({
          left: 0,
          behavior: "smooth"
        });
      } else {
        container.scrollTo({
          left: 0
        });
        this.$router.push("/");
      }
    },
    slide() {
      var container = document.getElementById("container");
      this.sideScroll(container, "right", 10, 250, 10);
    },
    slideBack() {
      var container = document.getElementById("container");
      this.sideScroll(container, "left", 10, 250, 10);
    },
    sideScroll(element, direction, speed, distance, step) {
      var scrollAmount = 0;
      var slideTimer = setInterval(function() {
        if (direction == "left") {
          element.scrollLeft -= step;
        } else {
          element.scrollLeft += step;
        }
        scrollAmount += step;
        if (scrollAmount >= distance) {
          window.clearInterval(slideTimer);
        }
      }, speed);
    }
  }
};
